import Vue from "vue";
import Router from "vue-router";
import store from "../assets/js/store";

import taobaoIndex from "@/pages/taobao/index"; //
import taobaoSearch from "@/pages/taobao/search"; //
import taobaoShare from "@/pages/taobao/share"; //
import taobaoLinkInto from "@/pages/taobao/linkInto"; //
import taobaoDetail from "@/pages/taobao/detail"; //

import jdDetail from "@/pages/jd/detail"; //
import jdIndex from "@/pages/jd/index"; //
import jdSearch from "@/pages/jd/search"; //
import jdShare from "@/pages/jd/share"; //

import pddDetail from "@/pages/pdd/detail"; //
import pddIndex from "@/pages/pdd/index"; //
import pddSearch from "@/pages/pdd/search"; //
import pddShare from "@/pages/pdd/share"; //
import pddLinkInto from "@/pages/pdd/linkInto"; //

import mtIndex from "@/pages/mt/index"; // 

import elmIndex from "@/pages/elm/index"; // 

import sunningIndex from "@/pages/sunning/index"; // 
import sunningSearch from "@/pages/sunning/search"; // 
import sunningDetail from "@/pages/sunning/detail"; // 
import sunningShare from "@/pages/sunning/share"; // 
import sunningLinkInto from "@/pages/sunning/linkInto"; // 


import wphgIndex from "@/pages/wph/index"; // 

import user from "@/pages/mine/user"; //
import commission from "@/pages/mine/commission"; //
import help from "@/pages/mine/help"; //
import invite from "@/pages/mine/invite"; //
import inviteRecord from "@/pages/mine/inviteRecord"; //
import order from "@/pages/mine/order"; //
import set from "@/pages/mine/set"; //
import changeName from "@/pages/mine/changeName"; //
import withdraw from "@/pages/mine/withdraw"; // 
import withdrawRecord from "@/pages/mine/withdrawRecord"; // 
import alipay from "@/pages/mine/alipay"; // 

import agreement from "@/pages/account/agreement"; // 
import privacy from "@/pages/account/privacy"; // 
import login from "@/pages/account/login"; //
import reg from "@/pages/account/reg"; //
import editPwd from "@/pages/account/editPwd"; //

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      name: "taobaoIndex",
      meta: {
        keepAlive: true
      },
      component: taobaoIndex //
    },
    {
      path: "/taobao/search",
      name: "taobaoSearch",
      meta: {
        keepAlive: true
      },
      component: taobaoSearch //
    },
    {
      path: "/taobao/share",
      name: "taobaoShare",
      component: taobaoShare //
    },
    {
      path: "/taobao/linkInto",
      name: "taobaoLinkInto",
      component: taobaoLinkInto //
    },
    {
      path: "/taobao/detail",
      name: "taobaoDetail",
      component: taobaoDetail //
    },
    {
      path: "/jd",
      name: "jdIndex",
      meta: {
        keepAlive: true
      },
      component: jdIndex //
    },
    {
      path: "/jd/search",
      name: "jdSearch",
      component: jdSearch //
    },
    {
      path: "/jd/detail",
      name: "jdDetail",
      component: jdDetail //
    },
    {
      path: "/jd/share",
      name: "jdShare",
      component: jdShare //
    },
    {
      path: "/pdd/detail",
      name: "pddDetail",
      component: pddDetail //
    },
    {
      path: "/pdd",
      name: "pddIndex",
      meta: {
        keepAlive: true
      },
      component: pddIndex //
    },
    {
      path: "/pdd/search",
      name: "pddSearch",
      component: pddSearch //
    },
    {
      path: "/pdd/share",
      name: "pddShare",
      component: pddShare //
    },
    {
      path: "/pdd/linkInto",
      name: "pddLinkInto",
      component: pddLinkInto //
    },
    {
      path: "/mt",
      name: "mtIndex",
      component: mtIndex //
    },
    {
      path: "/elm",
      name: "elmIndex",
      meta: {
        keepAlive: true
      },
      component: elmIndex //
    },
    {
      path: "/sunning",
      name: "sunningIndex",
      meta: {
        keepAlive: true
      },
      component: sunningIndex //
    },
    {
      path: "/sunning/search",
      name: "sunningSearch",
      component: sunningSearch //
    },
    {
      path: "/sunning/detail",
      name: "sunningDetail",
      component: sunningDetail //
    },
    {
      path: "/sunning/share",
      name: "sunningShare",
      component: sunningShare //
    },
    {
      path: "/sunning/linkInto",
      name: "sunningLinkInto",
      component: sunningLinkInto //
    },
    {
      path: "/wph",
      name: "wphgIndex",
      component: wphgIndex //
    },
    {
      path: "/user",
      name: "user",
      component: user //
    },
    {
      path: "/commission",
      name: "commission",
      meta: {
        login: true
      },
      component: commission //
    },
    {
      path: "/help",
      name: "help",
      component: help //
    },
    {
      path: "/invite",
      name: "invite",
      meta: {
        login: true
      },
      component: invite //
    },
    {
      path: "/inviteRecord",
      name: "inviteRecord",
      meta: {
        login: true
      },
      component: inviteRecord //
    },
    {
      path: "/order",
      name: "order",
      meta: {
        login: true
      },
      component: order //
    },
    {
      path: "/set",
      name: "set",
      meta: {
        login: true
      },
      component: set //
    },
    {
      path: "/changeName",
      name: "changeName",
      meta: {
        login: true
      },
      component: changeName //
    },
    {
      path: "/withdraw",
      name: "withdraw",
      meta: {
        login: true
      },
      component: withdraw //
    },
    {
      path: "/withdraw/record",
      name: "withdrawRecord",
      meta: {
        login: true
      },
      component: withdrawRecord //
    },
    {
      path: "/alipay",
      name: "alipay",
      meta: {
        login: true
      },
      component: alipay //
    },
    {
      path: "/agreement",
      name: "agreement",
      component: agreement //
    },
    {
      path: "/privacy",
      name: "privacy",
      component: privacy //
    },
    {
      path: "/editPwd",
      name: "editPwd",
      meta: {
        login: true
      },
      component: editPwd //
    },
    {
      path: "/login",
      name: "login",
      component: login //
    },
    {
      path: "/reg",
      name: "reg",
      component: reg //
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.meta.login === true) {
    //  
    if (store.state.user.id) {
      next();
    } else {
      next({ path: "/login" });
    }
  } else {
    next();
  }
});

export default router;



