<template>
  <div class="share-box">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="$router.go(-1)"
    />
    <div class="share-body">
      <template v-if="user.id">
        <div class="estimate"> <span class="c-red">{{ promotion_rate }}</span> </div>
      </template>
      <template v-else>
        <router-link class="c-red" :to="'/login?redirect=' + $route.fullPath"
          ></router-link
        >
      </template>
      <textarea class="content" :value="shareText" disabled> </textarea>
    </div>

    <div class="share-footer">
      <div class="share-footer-layer">
        <van-button
          type="warning"
          round
          size="large"
          @click="copy(material.mobile_short_url)"
          ></van-button
        >
        <van-button type="warning" round size="large" @click="copy(shareText)"
          ></van-button
        >
        <van-button type="danger" round size="large" @click="go()"
          ></van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      active: 1,
      material: {},
      goodInfo: {}, //
    };
  },
  created() {
     //  
    this.Post({},
      "/pdd/authorityQuery", (res) => {
      if (res.states === 1) {
const authority_query_response = res.data.authority_query_response || {}
      if(authority_query_response.bind === 1){
        this.init();
      }else if(authority_query_response.bind === 0){
        this.goAuthor();
      }
      }
    })
    //  
    this.Post({ goodSign: this.$route.query.id,searchId:this.$route.query.searchId}, "/pdd/goodsDetail", (res) => {
      if (res.states == 1) {
        let Data = res.data.goods_detail_response.goods_details[0];
        this.goodInfo = Data;
      }
    });
  },
  computed: {
    ...mapState(["rate", "user"]),
    shareText() {
      const text = `
${this.goodInfo.goods_name} \n
: ${this.goodInfo.min_normal_price / 100} \n
:${this.material.mobile_short_url}
`;
      return text;
    },
    // 
    promotion_rate() {
      return (
        ((this.goodInfo.promotion_rate / 1000) *
          (this.goodInfo.min_normal_price / 100) *
          this.rate) /
        100
      ).toFixed(2);
    },
  },
  methods: {
    // 
    goAuthor(){
       this.Post({},
      "/pdd/urlGenerate", (res) => {
        if(res.states === 1){
const authorLink =  res.data.rp_promotion_url_generate_response.url_list[0].mobile_url || ''
          window.location.href = authorLink
        } 
       })
    },
    init(){
      // 
    this.Post(
      {
        goodSign: this.$route.query.id,
        searchId: this.$route.query.searchId,
      },
      "/pdd/promotionUrlGenerate",
      (res) => {
        if (res.states === 1) {
          //           goods_promotion_url_list	OBJECT[]		
          // mobile_short_url	STRING		app
          // mobile_url	STRING		app
          // qq_app_info	OBJECT		qq
          // schema_url	STRING		schema
          // short_url	STRING		
          // url	STRING		
          // weibo_app_web_view_short_url	STRING		
          // weibo_app_web_view_url	STRING		
          // we_app_info	OBJECT		
          // we_app_web_view_short_url	STRING		app
          // we_app_web_view_url	STRING		app
          this.material =
            res.data.goods_promotion_url_generate_response.goods_promotion_url_list[0] || {}
        }
      });
    },
    go() {
      window.location.href = this.material.mobile_short_url;
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less" scoped>
@import "../../assets/less/share.less";
</style>



