<template>
  <div>
    <van-nav-bar title="" left-arrow @click-left="$router.go(-1)" />
    <van-cell-group>
      <van-field value="" label="" v-model="nickname" />
    </van-cell-group>
    <van-button type="danger" size="large" @click="changeName"></van-button>
  </div>
</template>


<script>
export default {
  data() {
    return {
      nickname: "", //
    };
  },
  created() {
    this.Post({}, "/main/userinfo", (res) => {
      if (res.states == 1) {
        this.nickname = res.data.nickname;
      } else if (res.states == 400) {
        this.$router.replace("/login");
      } else {
        this.Toast.fail(res.msg);
      }
    });
  },
  methods: {
    changeName() {
      this.Post(
        {
          nickname: this.nickname,
        },
        "/main/changeName",
        (res) => {
          if (res.states == 1) {
            this.Toast.success("");
          } else {
            this.Toast.fail(res.msg);
          }
        }
      );
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less" scoped>
</style>


