<template>
 <div>
     <van-nav-bar
  title=""
  left-arrow
  @click-left="$router.go(-1)"
/>
      <van-cell-group>
  <van-field
    value=""
     type="password"
    label=""
    v-model="old_password"
  />
  <van-field
    value=""
    label=""
    type="password"
    v-model="password"
  />
   <van-field
    value=""
    label=""
    type="password"
    v-model="confirm_password"
  />
</van-cell-group>
<van-button type="danger" size="large" @click="doEditPwd"></van-button>
 </div> 
</template>


<script>
export default {
   data() {
    return {
        old_password:'',
        password:'',
        confirm_password:'',
    };
  },
  methods:{
      doEditPwd(){
        this.Post({
            old_password:this.old_password,
            password:this.password,
            confirm_password:this.confirm_password,
        }, "/main/changepassword", res => {
            if (res.states == 1) {
                 this.Toast.success('');
               this.$router.replace('/login')
            }else{
                this.Toast.fail(res.msg);
            }
        });
      },
  } 
}
</script>

<style lang="less" rel="stylesheet/less" scoped>

</style>


