import Vue from 'vue';
import Vuex from 'vuex';
// Vuex.Store({
//   state,
//   actions,
//   mutation
// });
Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    platform: "1", //1PC2
    deposit: 0, // 
    rate: 0, // 
    agent: 0, // 
    user: window.localStorage.getItem("userInfo")
      ? JSON.parse(window.localStorage.getItem("userInfo"))
      : {}, // 
    tbGood: {}, // 
    pddGood: {}, // 
    jdGood: {} // // 
  },
  mutations: {
    // 
    setPlatform(state, data) {
      state.platform = data;
    },
    // 
    setConfig(state, data) {
      state.deposit = data.deposit;
      state.rate = data.rate;
      state.agent = data.agent;
    },
    // 
    setUser(state, data) {
      state.user = data;
    },
    // 
    setTb(state, data) {},
    // 
    setPdd(state, data) {},
    // 
    setJd(state, data) {}
  },
  actions: {
    // 
    setPlatform({ commit }, value) {
      commit("setPlatform", value);
    },
    setConfig({ commit }, data) {
      commit("setConfig", data);
    },
    setUser({ commit }, data) {
      commit("setUser", data);
    }
  }
});



